import { newSpecPage } from '@stencil/core/testing';
import { NylasMinCancellationNotice } from '../nylas-min-cancellation-notice';
import { h } from '@stencil/core';
import { SelectDropdown } from '../../../design-system/select-dropdown/select-dropdown';
import { TimePeriodSelector } from '../../../design-system/time-period-selector/time-period-selector';

describe('nylas-min-cancellation-notice', () => {
  let nylasMinCancellationNotice: NylasMinCancellationNotice;
  let page: any;
  beforeEach(async () => {
    page = await newSpecPage({
      components: [NylasMinCancellationNotice, TimePeriodSelector, SelectDropdown],
      template: () => {
        return <nylas-min-cancellation-notice name="min-cancellation-notice" minCancellationNotice={60}></nylas-min-cancellation-notice>;
      },
    });
    nylasMinCancellationNotice = page.rootInstance as NylasMinCancellationNotice;
  });

  it('renders', async () => {
    expect(page.root).toMatchSnapshot();
  });

  it('selects default min cancellation notice', async () => {
    const availableDaysNumberButton = page.root.shadowRoot.querySelector('select-dropdown#time-number').shadowRoot.querySelector('button');
    expect(availableDaysNumberButton.textContent).toBe('1');
    const availableDaysPeriodButton = page.root.shadowRoot.querySelector('select-dropdown#time-period').shadowRoot.querySelector('button');
    expect(availableDaysPeriodButton.textContent).toBe('hour');
  });

  it('selects min cancellation notice', async () => {
    const spyEvent = jest.fn();
    nylasMinCancellationNotice.valueChanged = {
      emit: spyEvent,
    };
    const mockEvent = new CustomEvent('timePeriodChanged', {
      detail: { number: 2, period: 'week' },
      bubbles: true,
      composed: true,
    });
    nylasMinCancellationNotice.timePeriodChangedHandler(mockEvent);
    expect(spyEvent).toHaveBeenCalled();
  });
});
